//SCSS
//変数を利用してもっとわかりやすいCSSを構成可能。コードの量が減る。
@mixin Sample()  {
    background: #eeeeee;
    padding: 10px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: auto;
}

$MainColor : #ff0000;
.red {
    color: MainColor;
}

.product-alert {
    background: #eeeeee;
    padding: 12px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: auto;
}

.product-alert p{
    margin-bottom: 0;
    @include Sample()
}

.product-alert-other {
    @extend .product-alert;
    background: #ffee70

}


